import Jspdf from 'jspdf'

export default {
  createContratPdf (employeur, tuteurs, assMat, enfant, contrat, presences) {
    // Crer le tableau des tuteurs
    var t = ''
    t += '<table id="t03">'
    t += '<tr>'
    for (var i = 0; i < tuteurs.length; i++) {
      t += `<th>${tuteurs[i].nom_type_tuteur} : ${tuteurs[i].prenom_tuteur + ' ' + tuteurs[i].nom_tuteur} </th>`
    }
    t += '</tr>'
    t += '<tr>'
    for (var j = 0; j < tuteurs.length; j++) {
      t += `<td> Profession : ${tuteurs[j].profession} </td>`
    }
    t += '</tr>'
    t += '<tr>'
    for (var k = 0; k < tuteurs.length; k++) {
      t += `<td> Tlphone : ${tuteurs[k].telephone} </td>`
    }
    t += '</tr>'
    t += '<tr>'
    for (var l = 0; l < tuteurs.length; l++) {
      t += `<td> Tlphone professionnel : ${tuteurs[l].telephone_pro} </td>`
    }
    t += '</tr>'
    t += '</table>'

    // Crer le tableau des prsences
    var p = ''
    p += '<table id="t01">'
    p += '<tr>'
    p += '<th>Jour</th>'
    p += '<th>Lundi</th>'
    p += '<th>Mardi</th>'
    p += '<th>Mercredi</th>'
    p += '<th>Jeudi</th>'
    p += '<th>Vendredi</th>'
    p += '<th>Samedi</th>'
    p += '<th>Dimanche</th>'
    p += '</tr>'
    p += '<tr>'
    p += '<td> Heure d arrive </td> '
    for (var m = 0; m < presences.length; m++) {
      p += `<td> ${presences[m].heureArrivee} </td>`
    }
    p += '</tr>'
    p += '<tr>'
    p += '<td> Heure de dpart </td> '
    for (var n = 0; n < presences.length; n++) {
      p += `<td> ${presences[n].heureDepart} </td>`
    }
    p += '</tr>'
    p += '</table>'

    var nbSemaineAccueil = 52 - assMat.nombreSemaineConges - employeur.nombreSemSupp
    var salaireMensuelNet = (contrat.nb_heures_semaine * contrat.tarif * nbSemaineAccueil) / 12
    var htmlP1 = `<!DOCTYPE html>
<html lang="en">
<head>

    <meta charset="UTF-8">
    <center><h2><u>Renseignements personnels</u></h2></center>
    <p> <u> Identification de l'employeur :</u> <i> En qualit de : </i></p>
    <p><i>  <span> Nom de Naissance : ${employeur.nomNaissanceEmp} </span> &nbsp; <span> Nom d'usage : ${employeur.nomUsageEmp}</span> &nbsp; <span> Prnom : ${employeur.prenomEmp} </span> </i> </p>
    <p><i>  Adresse :  ${employeur.rueEmp + ' ' + employeur.codePEmp + ' ' + employeur.villeEmp}</i> </p>
    <p><i>  <span> Tlphone du domicile : ${employeur.telephoneEmp}</span> &nbsp; <span> Courriel : ${employeur.emailEmp}</span> </i> </p>
    ${t}

    <p> <u> Et la salarie :</u>
    <p><i>  <span> Nom de Naissance : ${assMat.nomNaissanceAssMat}</span> &nbsp; <span> Nom d'usage : ${assMat.nomUsageAssMat}</span> &nbsp; <span> Prnom : ${assMat.prenomAssMat} </span> </i> </p>
    <p><i> <span> Ne le : ${assMat.dateNaissanceAssMat}</span> &nbsp; <span>  : ${assMat.villeNaissanceAssMat}</span>  </i> </p>
    <p><i>  Numro de SS : ${assMat.numeroSS} </i> </p>
    <p><i>  <span>  Agrment dlivr le : ${assMat.dateAgrement} </span> &nbsp; <span> Rfrence de l'agrment : ${assMat.referenceAgrement} </span> </i> </p>
    <p><i>  <span> Assurance "Responsabilit Civile Professionnelle" : ${assMat.assurance} </span></i> </p>
    <p><i>  Numro de Police : ${assMat.numeroPolice} </i> </p>
`
    let htmlP2 = `
    <center><h2><u>Les termes du contrat :</u></h2></center>
    <p> Il est conclu un contrat de travail rgi par les dispositions de la convention collective nationale de travail des assistantes maternelles du particulier employeur. L'employeur s'assure que le salari possde un exemplaire de cette convention  jour. </p>
    <p><i> <span>Ce contrat de travail  dure indtermine est tabli pour l'enfant : ${enfant.prenomEnfant + ' ' + enfant.nomEnfant} </span> &nbsp; <span> N(e) le : ${enfant.dateNaissanceEnfant}</span> </i> </p>
    <p> <i> <b> DATE D'EFFET DU CONTRAT </b> ( compter du premier jour de la priode d'essai) : ${contrat.dateDebAdapt} </i> </p>
    <p> <u> Priode d'essai : </u> </p>
    <ul>
        <li>Dure :  </li>
        <li>Modalits de la priode d'adaptation : </li>
    </ul>
    <p> <u> Dure et horaire d'accueil : </u> </p>
    <ul>
        <li>Horaire hebdomadaire : </li>
            <p> nombre d'heures : &nbsp;${contrat.nb_heures_semaine} /semaine, selon le planning suivant : </p>
${p}
        <li>Accueil annuel : </li>
            <p> Nombre de semaine d'accueil : ${nbSemaineAccueil} semaines </p> 
    </ul>
    <p> <u> Rmunration : </u> </p>
    <ul>
        <li>Salaire horaire de base </li>
            <p> Salaire horaire net de base : ${contrat.tarif} euros </p> 
        <li>Salaire de base : le salaire est mensualis. </li>
            <p> Salaire mensuel net : ${salaireMensuelNet} euros </p> 
        <li>Salaire major ( partir de la 46me heure hebdomadaire) : </li>
            <p> Montant de la majoration : ${contrat.taux_majore} %  Salaire net horaire major :  </p>
        <li> Date de paiement du salaire : <b> le ${contrat.jour_paiement} de chaque mois </b> Mode de paiement du salaire : ${contrat.modeDePaiementContrat} </li>
    </ul>`
    let htmlP3 = `
    <p> <u> Congs pays : </u> </p>
    <ul>
        <li> Les parties conviennent que l'assistante maternelle prend des congs avant la fin de la premire anne </li> de rfrence. Si l'assistante maternelle prend plus de jours que ceux convenu au contrat. Elle les prend sans rmunration. Il est convenu d'un accord commun pour :
            <p> Le salari :  ${assMat.nombreSemaineConges} de cong annuel </p>
            <p>L'employeur : ${employeur.nombreSemSupp} de cong annuel en plus de son assistante maternelle  </p> 
        <li> Le dlai de prvenance du cong principal est  fixer au plus tard le 1er mars. </li> Si les dates ne sont pas connues  la signature du contrat, un dlai de prvenance est fix : 1 mois <br>
        <li> Modalits de paiement des congs en cas d'anne incomplte : Chaque mois. </li> La mthode des 10% est applique sur le salaire net. <br>
        <li> Modalits de paiement des congs en cas d'anne complte : <u> dj inclus dans la mensualisation </u> </li>
    </ul>
    <p> <u> Jours fris : </u> </p>
    <ul>
        <li> Seul le 1er mai est chm et pay, si ce jour prcis est habituellement travaill. </li> La Maison d'Assistantes Maternelles tant ferme les autres jours fris, aucun accueil ne sera effectu. Cependant le chmage des jours habituellement travaills ne pourra tre la cause d'une diminution de la rmunration. 
    </ul>
    <p> <u> Indemnits d'entretien et de frais de repas : </u> </p>
    <ul>
        <li> Indemnit d'entretien, montant journalier : 4 euros par jour de prsence. </li> Les indemnits d'entretien sont mensualises <br>
        <li> Goter, montant journalier : 1 euro par jour de prsence </li>
    </ul>
    <p> <u> Dlgation : </u> </p>
    <ul>
        <li> Le parent confie son enfant  une assistante maternelle exerant en Maison d'Assistantes Maternelles, </li> il autorise cette assistante  dlguer l'accueil de son enfant  d'autres assistantes maternelles exerant dans la M.A.M Loulou Trottinette.
    </ul> `
    let htmlP4 = `
    <p> <u> Modification du contrat en cours : </u> </p>
    <ul>
        <li> Le contrat de travail peut tre modifi. Toutefois, aucune modification ne pourra tre dcide. </li> 
        Il est tenu de laisser un dlai suffisant d'un minimum de 15 jours, pour connatre son refus ou son acceptation. Les deux parties rpondent par crit : s'ils sont d'accord, il y aura un avenant du conrat, s'il n'y a pas d'accord il peut y avoir rupture de contrat. Soit dmission de l'assistante maternelle. Soit licenciement par employeur. <br>
        <li> Si l'assistante maternelle n'accepte pas le changement, l'employeur peut revenir </li>
         aux conditions initiales du contrat ou y mettre fin. Le fait pour le salari de ne pas avoir accept la modification propose ne le prive pas de pravis. <br>
        <li> Toute modification doit faire l'objet d'un avenant imprativement dat et sign par les deux contractants. </li>
    </ul> 

    <p> <b> Signature de l'employeur </b> </p> 
    <p>(Prcde de la mention "Lu et approuv")</p>
    <p> A  : </p>
    <p> Le : </p>
    
    <p> <b> Signature du salari </b> </p> 
    <p>(Prcde de la mention "Lu et approuv")</p>
    <p> A  : </p>
    <p> Le : </p>
    
</head>
<body>

</body>
</html>`
    let pdfName = 'contrat'
    var doc = new Jspdf('p', 'pt', 'letter')
    var specialElementHandlers = {'#bypassme': function (element, renderer) {
      return true
    }
    }
    var margins = {
      bottom: 60,
      left: 40,
      width: 522
    }
    doc.fromHTML(htmlP1, margins.left, {width: margins.width, 'elementHandlers': specialElementHandlers}, margins)
    doc.addPage()
    doc.fromHTML(htmlP2, margins.left, {width: margins.width, 'elementHandlers': specialElementHandlers}, margins)
    doc.addPage()
    doc.fromHTML(htmlP3, margins.left, {width: margins.width, 'elementHandlers': specialElementHandlers}, margins)
    doc.addPage()
    doc.fromHTML(htmlP4, margins.left, {width: margins.width, 'elementHandlers': specialElementHandlers}, margins)
    doc.save(pdfName + '.pdf')
  }
}



